@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Raleway', sans-serif;
    scroll-behavior: smooth;
  }
}

body {
  margin: 0;
  padding: 0;
  background-color: #0A192F;
}

/* Custom utilities for creative, professional UI */
@layer utilities {
  .bg-grid-dark {
    position: relative;
    background-color: #0A1024;
    background-image:
      linear-gradient(to right, rgba(139, 92, 246, 0.08) 1px, transparent 1px),
      linear-gradient(to bottom, rgba(139, 92, 246, 0.08) 1px, transparent 1px),
      radial-gradient(ellipse at top, rgba(139, 92, 246, 0.12), transparent 40%),
      radial-gradient(ellipse at bottom, rgba(39, 39, 42, 0.6), transparent 40%);
    background-size: 24px 24px, 24px 24px, 100% 50%, 100% 50%;
    background-position: 0 0, 0 0, 0 0, 0 100%;
  }
  .card-grid {
    @apply relative rounded-2xl border border-dashed border-secondary/50 bg-white/5 backdrop-blur-xl shadow-[0_8px_40px_rgba(0,0,0,0.25)];
  }
  .badge-pill {
    @apply inline-block px-4 py-1.5 rounded-full text-sm font-bold text-black;
    background: linear-gradient(90deg, #FDE68A 0%, #A7F3D0 100%);
    filter: drop-shadow(0 2px 10px rgba(0,0,0,0.15));
  }
  .badge-pill.secondary {
    background: linear-gradient(90deg, #F9A8D4 0%, #C4B5FD 100%);
  }
  .gradient-text {
    background: linear-gradient(90deg, #FFFFFF 0%, #A78BFA 50%, #34D399 100%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }
} 